
















import {Component, Prop, Vue} from "vue-property-decorator"
import {IAvailability} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import TrainerHours from "@/components/TrainerHours.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { TrainerHours } })
export default class UserAccount extends Vue {
	private targetDate: Date | null = null
	private targetDay: string | null = null
	private targetAvailability: IAvailability[] | null = null

	private availabilities: IAvailability[] | null = null

	private daysInWeek: string[] = [i18n.t("sunday").toString(), i18n.t("monday").toString(),
		i18n.t("tuesday").toString(), i18n.t("wednesday").toString(), i18n.t("thursday").toString(),
		i18n.t("friday").toString(), i18n.t("saturday").toString()]

	private mounted(): void {
		this.getAvailabilities()
	}

	/**
	 * @close TrainerHours
	 */

	private closeHours(): void {
		this.targetDate = null
		this.getAvailabilities()
	}

	/**
	 * Get availabilities
	 */

	private getAvailabilities(): void {
		Portal.client.call.booking.availabilityGet()
			.response
			.then(result => {
				this.availabilities = result
				!!result.length
					? this.$emit("isAvailable", true)
					: this.$emit("isAvailable", false)
			})
	}

	/**
	 * Check availability for specific day
	 */

	private checkAvailability(dayId: number) {
		const date = new Date()
		date.setDate(date.getDate() + dayId)
		let daySlots: IAvailability[]
		let available: string = ``

		if (this.availabilities) {
			daySlots = this.availabilities.filter(el =>
				new Date(el.TimeSlot.Start).getDate() === date.getDate())

			if (!daySlots)
				return null

			daySlots.forEach(el => {
				const startHour: number = new Date(el.TimeSlot.Start).getHours()
				const startMinutes: string = new Date(el.TimeSlot.Start).getMinutes().toString()
				const endHour: number = new Date(el.TimeSlot.End).getHours()
				const endMinutes: string = new Date(el.TimeSlot.End).getMinutes().toString()
				available += `${this.amPm(startHour, startMinutes)}-${this.amPm(endHour, endMinutes)}; `
			})

			return available.slice(0, -2)
		}
	}

	/**
	 * Set availability for target date
	 */

	private setAvailability(dayId: number): void {
		this.targetDay = this.dayLabel(dayId)
		this.targetDate = new Date()
		this.targetDate.setDate(this.targetDate.getDate() + dayId)
		const compareDate = this.targetDate

		if (this.availabilities)
			this.targetAvailability = this.availabilities.filter(el =>
				new Date(el.TimeSlot.Start).getDate() === compareDate.getDate())
	}

	/**
	 * @returns i.e. Friday 4/19
	 */

	private dayLabel(dayId: number): string {
		let day: string = ""
		const date = new Date()
		date.setDate(date.getDate() + dayId)

		switch (dayId) {
			case 0: day = "Today"; break
			case 1: day = "Tomorrow"; break
			default: day = this.daysInWeek[date.getDay()]
		}

		return `${day} ${date.getMonth() + 1}/${date.getDate()}`
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, minutes: string): string {
		minutes = minutes === "0" ? `` : `:${minutes}`
		if (hour > 12)
			return `${hour % 12}${minutes}pm`
		else if (hour < 12)
			return `${hour}${minutes}am`
		return `12pm`
	}
}
