






































import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import {IAvailability} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import NotificationMessage from "@/components/NotificationMessage.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

interface ITimeSlot {
	fromHour: number
	fromMinute: number
	fromAmPm: string
	toHour: number
	toMinute: number
	toAmPm: string
}

@Component({ components: { NotificationMessage } })
export default class TrainerHours extends Vue {
	@Prop()
	private date!: Date

	@Prop()
	private day!: string

	@Prop()
	private availability!: IAvailability[]

	private notificationMsg: TranslateResult | null = null

	private countTimeSlots: number = 1
	private maxTimeSlots: number = 3
	private timeSlots: ITimeSlot[] = []

	private created(): void {
		if (this.availability.length) {
			this.countTimeSlots = this.availability.length
			this.availability.forEach((el, idx) => {
				const timeSlot: ITimeSlot = {} as any
				const start: number = new Date(el.TimeSlot.Start).getHours()
				const end: number = new Date(el.TimeSlot.End).getHours()

				timeSlot.fromHour = this.format12hour(start)
				timeSlot.fromMinute = new Date(el.TimeSlot.Start).getMinutes()
				timeSlot.fromAmPm = this.amPm(start)
				timeSlot.toHour = this.format12hour(end)
				timeSlot.toMinute = new Date(el.TimeSlot.End).getMinutes()
				timeSlot.toAmPm = this.amPm(end)

				this.timeSlots.push(timeSlot)
			})
		} else
				for (let n = 0; n < this.countTimeSlots; n++)
					this.timeSlots.push({} as any)
	}

	/**
	 * Time Slot start/end time
	 */

	private setTimeSlot(): void {
		const start: Date = new Date(this.date)
		const end: Date = new Date(this.date)

		this.timeSlots.forEach((slot, idx) => {
			if (slot.fromHour) {
				const startHour: number = this.format24hour(slot.fromHour, slot.fromAmPm)
				const endHour: number = this.format24hour(slot.toHour, slot.toAmPm, true)

				start.setHours(startHour, 0, 0, 0)
				start.setMinutes(slot.fromMinute, 0, 0)
				end.setHours(endHour, 0, 0, 0)
				end.setMinutes(slot.toMinute, 0, 0)

				this.postAvailability(start, end)
			}
		})
	}

	/**
	 * Post availability
	 */

	private postAvailability(start: Date, end: Date): void {

		Portal.client.call.booking.availabilityPost(start, end)
			.response
			.then(() => this.$emit("close"))
			.catch(() => this.notificationMsg = i18n.t("trainerHours.text4"))
	}

	/**
	 * Delete availability
	 */

	private becomeUnavailable(): void {
		this.availability.forEach(slot =>
			Portal.client.call.booking.availabilityDelete(slot.Id)
				.response
				.then(() => this.$emit("close"))
		)
	}

	/**
	 * Add Time Slot
	 */

	private addTimeSlot(): void {
		this.countTimeSlots++
		this.timeSlots.push({} as any)
	}

	/**
	 * Helpers
	 */

	private slotsExist(): boolean {
		return !!this.availability.length
	}

	private format24hour(hour: number, amPm?: string, max: boolean = false): number {
		const twelve = max ? 24 : 0
		return amPm === "pm"
			? Number(hour) === 12 ? 12 : Number(hour) + 12
			: Number(hour) === 12 ? twelve : hour
	}

	private format12hour(hour: number): number {
		return Number(hour) === 0
			? 12
			: hour > 12 ? hour - 12 : hour
	}

	private amPm(hour: number): string {
		return hour >= 12 ? "pm" : "am"
	}
}
